import React from 'react';
import ReactMarkdown from 'react-markdown';

// CSS files
import './Section.css';
import 'github-markdown-css/github-markdown.css';

// const
const IS_DEV = true;

// get location of images folder in webpack
const imageContext = require.context('../assets/images', true);
// also get location of the markdown text files to use
var markdownContext;
if(process.env.NODE_ENV === 'development')
    markdownContext = require.context('../../public/markdown_docs/', false, /\.md$/);// grab uri of each markdown file in here
else
    markdownContext = require.context('./markdown_docs/', false, /\.md$/);// grab uri of each markdown file in here

const markdownFiles = markdownContext
    .keys()
    .map((filename) => markdownContext(filename));
console.log(markdownFiles);

class Section extends React.Component {
    constructor(props) {
        console.log("Section " + props.name);
        // props are still accessible through super constructor
        super(props);
        // default set state for derived variables
        this.state = {
            fileURL: '',
            thumbnailURL: '',
            text: ''
        }

        // generate thumbnail
        if(props.thumbnailURL !== '')
            this.state.thumbnailURL = imageContext('./thumbnails/' + props.thumbnailURL);
        else
            this.state.thumbnailURL = imageContext('./thumbnails/moon2.png');

        // generate section text blurb
        // first, find the url of the matching section
        var that = this;
        markdownFiles.forEach(function(file) {
            if(file.includes(props.sectionID))
                that.state.fileURL = file;
        });
    }

    // reactjs.org says that this function is a good place to load remote data, say from your github server
    componentDidMount(){
        // load that file
        console.log(this.state.fileURL);
        fetch(this.state.fileURL)
            .then(result => result.text())
            .then(result => this.setState({text: result}));
    }

    render(){
        // redirect to port 8000 if in dev
        let url = "";
        if(IS_DEV)
            url = "http://localhost:8000/" + this.props.sectionID + ".html";
        // else default url
        else
            url = "https://dimembermatt.github.io/Generative_Art/" + this.props.sectionID + ".html";
        return (
            <div className="section">
                <img className="thumbnail" src={this.state.thumbnailURL} alt={this.props.thumbnailURL}></img>
                <div className="details">
                    <ReactMarkdown className="markdown-body" source={this.state.text}/>
                    {/* normal link to a autogenerated component using the markdown file. */}
                    <a href={url}>Read more...</a>
                </div>
            </div>
        );
    }
}

export default Section;
